.weatherLocation {
    color: #888;
    font-family: 'Roboto', sans-serif;
    -webkit-box-shadow: 10px 10px 42px -12px;
            box-shadow: 10px 10px 42px -12px;
    margin-left: 10px;
    margin-right: 10px;
    padding: 12px;
    -webkit-box-shadow: border-box;
            box-shadow: border-box;
    width: calc(100% - 20px);
    min-width: 430px;
    
}

.locationCont {
    width: 100%;
    height: 40px;
}

.locationCont h1 {
    margin: 0;
}

.forecastItem{
    background-color: teal;
}



